<template>
  <form>
    <md-card>
      <md-card-header :data-background-color="dataBackgroundColor">
        <h4 class="title">Edit Profile</h4>
        <p class="category">Complete your profile</p>
      </md-card-header>
      <md-card-content>
        <div class="md-layout">
          <div class="md-layout-item md-small-size-100 md-size-50">
            <md-field>
              <label> Name</label>
              <md-input v-model="name" type="text"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-50 md-size-50">
            <md-field>
              <label>Email Address</label>
              <md-input v-model="emailadress" type="email"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-50 md-size-50">
            <md-field>
              <label>Position</label>
              <md-input v-model="position" type="email"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-100">
            <md-field :class="messageClass">
              <label>Upload Photo</label>
              <md-file @change="getImage" placeholder="Upload photo"/>
              <span class="md-error">max size 2MB</span>
            </md-field>
          </div>
          <div class="md-layout-item md-size-100">
            <md-field maxlength="20">
              <label>About Me</label>
              <md-textarea v-model="aboutme"></md-textarea>
            </md-field>
          </div>
          <div class="md-layout-item md-size-100 text-right">
            <md-button v-if="!error" class="md-raised md-success" @click="onUpload">Update Profile</md-button>
            <md-button v-else class="md-raised md-success" disabled>Update Profile</md-button>
          </div>
        </div>
      </md-card-content>
    </md-card>
  </form>
</template>
<script>
import axios from "axios";

export default {
  name: "edit-profile-form",
  props: {
    dataBackgroundColor: {
      type: String,
      default: ""
    },
  },
  data() {
    return {
      disabled: null,
      name: null,
      emailadress: null,
      image: null,
      position: null,
      aboutme: '',
      error: false,
    };
  },
  computed: {
    messageClass() {
      return {
        "md-invalid": this.error,
      };
    },
  },
  methods: { getImage(event) {
      this.image = event.target.files[0]
      console.log(this.image.size)
      if (this.image.size > 2097152) {
        this.error = true
      } else {
        this.error = false
      }
    },

    onUpload() {
      const fd = new FormData();
      fd.append('image', this.image)

      axios.post('/user/image/upload/' + 0, fd)

      axios.put('/user/update/' + 0, {
        user: {
          "name": this.name ? this.name : this.$store.state.user.name,
          'email': this.emailadress ? this.emailadress : this.$store.state.user.email,
          'about': this.aboutme ? this.aboutme : this.$store.state.user.about,
          'position': this.position ? this.position : this.$store.state.user.position,
          'access_level': this.$store.state.user.access_level
        }
        // eslint-disable-next-line no-unused-vars
      }).then(response => {
        this.$store.dispatch('retrieveUserPhoto')
        this.$store.dispatch('retrieveUser')
      })
    }
  }
};
</script>
