<template>
  <div>
    <md-dialog :md-active.sync="showDialog">
      <md-dialog-content class="lead-update-content">
        <div class="md-layout lead-update-form">
          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change name</label>
              <md-input v-model="lead.name" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Email</label>
              <md-input v-model="lead.email" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Telephone</label>
              <md-input v-model="lead.telephone" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-datepicker v-model="lead.date" md-immediately :md-model-type="String" @change="updateLead(lead)">
              <label>Change Date</label>
            </md-datepicker>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Time Landing</label>
              <md-input v-model="lead.time_landing" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Time</label>
              <md-input v-model="lead.time" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Aktion</label>
              <md-input v-model="lead.aktion" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Quelle</label>
              <md-input v-model="lead.quelle" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Call to Action</label>
              <md-input v-model="lead.call_to_action" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Page</label>
              <md-input v-model="lead.page" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change URL</label>
              <md-input v-model="lead.url" @blur="updateLead(lead)" @keyup.enter="updateLead(lead)">
              </md-input>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100 md-size-33">
            <md-field>
              <label>Change Status</label>
              <md-select v-model="lead.status">
                <md-option value="Neu">Neu</md-option>
                <md-option value="Bearbeitung">Bearbeitung</md-option>
                <md-option value="Abgesclossen">Abgesclossen</md-option>
              </md-select>
            </md-field>
          </div>

          <md-dialog-actions>
            <md-button class="md-primary" @click="showDialog = false">Close</md-button>
            <md-button class="md-primary" @click="updateLead(lead)">Save</md-button>
          </md-dialog-actions>
        </div>
        <div class="comment-from">
          <h3>Kommenter </h3>

          <md-field>
            <label>Your comment</label>
            <md-textarea v-model="comment"></md-textarea>
          </md-field>
          <md-button class="md-primary" @click="postComment(lead.guid)">Senden</md-button>
          <md-content class="md-scrollbar">
            {{ showComment }}
          </md-content>
          <h6>Author : {{ authorName }}</h6>
        </div>
      </md-dialog-content>
    </md-dialog>
    <button @click="showModal(lead.guid)">
      <b-icon-pencil font-scale="1.5"></b-icon-pencil>
    </button>
  </div>
</template>


<script>

export default {
  name: 'TableCard',
  props: {
    dataBackgroundColor: {
      type: String,
      default: "green"
    },
    lead: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      data: null,
      author: null,
      comment: null,
      showDialog: false,
      name: this.lead.name,
      email: this.lead.email,
      telephone: this.lead.telephone,
      date: this.lead.date,
      timeLanding: this.lead.time_landing,
      time: this.lead.time,
      aktion: this.lead.aktion,
      quelle: this.lead.aktion,
      callToAction: this.lead.call_to_action,
      page: this.lead.page,
      url: this.lead.url,
      status: this.lead.status,
      guid: this.lead.guid
    }
  },
  computed: {
    showUser() {
      return this.$store.getters.retrieveUser;
    },
    authorName() {
      return this.$store.getters.retrieveAuthor
    },
    showComment() {
      return this.$store.getters.retrieveComment
    }
  },
  methods: {
    updateLead(lead) {
      this.$store.dispatch('updateLead', lead)
    },

    fetchComment(commentId) {
      this.$store.dispatch('fetchComment', commentId)
    },
    postComment(guid) {
      console.log(this.comment)
      this.$store.dispatch('postComment', {
        "guid": guid,
        'comment': this.comment
      })
          .then(this.fetchComment(guid))

    },
    showModal(guid) {
      this.showDialog = true
      this.fetchComment(guid)
    }
  },
  created() {
    this.$store.dispatch('retrieveUser')
  }
}
</script>
