<template>
  <div>
    <div>
      <b-button class="filter-buttons" :class="{ active: filter == 'all' }" @click="changeFilter('all')" pill
                variant="outline-secondary">Alle
      </b-button>
      <b-button class="filter-buttons" :class="{ active: filter == 'Neu' }" @click="changeFilter('Neu')" pill
                variant="outline-secondary">Neu
      </b-button>
      <b-button class="filter-buttons" :class="{ active: filter == 'Bearbeitung' }" @click="changeFilter('Bearbeitung')"
                pill
                variant="outline-secondary">Bearbeitung
      </b-button>
      <b-button class="filter-buttons" :class="{ active: filter == 'Abgesclossen' }" @click="changeFilter('Abgesclossen')" pill
                variant="outline-secondary">Abgesclossen
      </b-button>
      <b-button class="filter-buttons" :class="{ active: filter == 'Wiedervorlage' }" @click="changeFilter('Wiedervorlage')" pill
                variant="outline-secondary">Wiedervorlage
      </b-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'todo-filtered',
  computed: {
    filter() {
      return this.$store.state.filter
    }
  },
  methods: {
    changeFilter(filter) {
      this.$store.dispatch('updateFilter', filter)
      this.$store.dispatch('retrieveLeads')
    }
  }
}
</script>
<style>
.filter-buttons {
  margin-right: 10px;
}
</style>
