<template>
  <div>
    <md-dialog :md-active.sync="showDialog">
      <md-dialog-content>
        <h3 class="text-center">Delete {{ user.name }}?</h3>
      </md-dialog-content>
      <md-dialog-actions>
        <md-button class="md-primary" @click="showDialog = false">No</md-button>
        <md-button class="md-primary" @click="deleteUser(user)">Yes</md-button>
      </md-dialog-actions>
    </md-dialog>
    <div @click="showDialog = true" class="icons">
      <md-icon>delete_forever</md-icon>
    </div>
  </div>
</template>

<script>

export default {
  components: {
  },
  props: {
    showDialog: {
      type: Boolean,
      required: true,
    },
    user: {
      type: Object,
      required: true,
    },
  },
  methods:{
    deleteUser(user){
      this.showDialog = false
      this.$store.dispatch('deleteUser', user)
    }
  }
}
</script>