<template>
  <b-container fluid>
    <b-input-group size="sm" class="table-top">
      <b-form-input
          id="filter-input"
          v-model="filter"
          type="search"
          placeholder="Type to Search"
      ></b-form-input>
    </b-input-group>
    <!-- Main table element -->
    <b-table
        :items="showHistory"
        :fields="fields"
        :current-page="currentPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        stacked="md"
        show-empty
        small
    >
      <template #row-details="row">
        <b-card>
          <ul>
            <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
          </ul>
        </b-card>
      </template>
    </b-table>
  </b-container>
</template>

<script>
export default {
  name: 'History',
  data() {
    return {
      fields: [
        {key: 'user_id', label: 'User Name', sortable: true, class: 'text-center'},
        {key: 'auditable_id', label: 'Lead Id', sortable: true, class: 'text-center'},
        {key: 'event', label: 'event', sortable: true, class: 'text-center', sortDirection: 'desc'},
        {key: 'old_values', label: 'From', sortable: true, class: 'text-center'},
        {key: 'new_values', label: 'To', sortable: true, class: 'text-center'},
        {key: 'created_at', label: 'Date', sortable: true, class: 'text-center'},
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 150,
      pageOptions: [5, 10, 15, {value: 100, text: "Show a lot"}],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
    }
  },
  computed: {
    showHistory() {
      return this.$store.getters.retrieveHistory
    }
  },
  methods: {
    retrieveHistory() {
      this.$store.dispatch('retrieveHistory')
    }
  },
  created() {
    this.retrieveHistory()
  }
}
</script>