<template>
  <md-card class="md-card-profile">
    <div class="md-card-avatar">
      <img class="img" :src="`${this.$store.state.userURL}${this.$store.state.userPhoto}`"/>
    </div>
    <md-card-content>
      <h6 class="category text-gray">{{ showFullUser.position }} </h6>
      <h6 class="category text-gray">{{ showFullUser.access_level }} </h6>
      <h4 class="card-title">{{ showUser.name }}</h4>
      <p class="card-description">
        {{ showFullUser.about }}
      </p>
      <md-button class="md-round md-success" @click="showUpdateForm">Update profile</md-button>
    </md-card-content>
  </md-card>
</template>
<script>

export default {
  name: "user-card",
  data() {
    return {
      image: null,
      photo: null,
    };
  },
  computed: {
    showUpdate() {
      return this.$store.getters.showUpdate
    },
    showUser() {
      return this.$store.getters.retrieveUser;
    },
    showFullUser() {
      return this.$store.getters.retrieveUser
    }
  },
  methods: {
    showUpdateForm() {
      this.$store.dispatch('showUpdate')
    },
  },
  created() {
    this.$store.dispatch('retrieveUserPhoto')
    // this.$store.dispatch('retrieveUserName')
    this.$store.dispatch('retrieveUser')
  }
};
</script>

