import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)
axios.defaults.baseURL = 'https://leads-oncymstoua-uc.a.run.app/api'
export const store = new Vuex.Store({
    state: {
        token: localStorage.getItem('access_token') || null,
        userURL: 'https://leads-oncymstoua-uc.a.run.app',
        filter: 'all',
        showUpdate: false,
        user: {},
        users: null,
        userPhoto: null,
        leads: [],
        history: [],
        author: null,
        comment: null,
    },
    getters: {
        loggedIn(state) {
            return state.token !== null
        },
        leadsFiltered(state) {
            if (state.filter === "all") {
                return state.leads
            } else if (state.filter === 'Neu') {
                return state.leads.filter(lead => lead.status === "Neu")
            } else if (state.filter === 'Bearbeitung') {
                return state.leads.filter(lead => lead.status == 'Bearbeitung')
            } else if (state.filter == 'Abgesclossen') {
                return state.leads.filter(lead => lead.status == 'Abgesclossen')
            } else if (state.filter == 'Wiedervorlage') {
                return state.leads.filter(lead => lead.resubmission)
            }
        },
        showUpdate(state) {
            return state.showUpdate
        },
        retrieveUser(state) {
            return state.user
        },
        retrieveUsers(state) {
            return state.users
        },
        retrieveHistory(state) {
            return state.history
        },
        retrieveAuthor(state) {
            return state.author
        },
        retrieveComment(state) {
            return state.comment
        },
        retrieveAccess(state) {
            return state.user.access_level
        }
    },
    mutations: {
        retrieveUser(state, user) {
            state.user = user
        },
        retrieveUsers(state, users) {
            state.users = users
        },
        retrieveUserPhoto(state, userPhoto) {
            state.userPhoto = userPhoto
        },
        updateFilter(state, filter) {
            state.filter = filter
        },
        updateGroupFilter(state, groupFilter) {
            state.groupFilter = groupFilter
        },
        retrieveLeads(state, leads) {
            state.leads = leads
        },
        fetchComment(state, comment) {
            state.comment = comment
        },
        retrieveAuthor(state, author) {
            state.author = author
        },
        retrieveHistory(state, history) {
            state.history = history
        },
        retrieveToken(state, token) {
            state.token = token
        },
        destroyToken(state) {
            state.token = null
        },
        showUpdate(state, showUpdate) {
            state.showUpdate = showUpdate
        },
    },
    actions: {
        //USER AND AUTH
        retrieveUser(context) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/user')
                .then(response => {
                    console.log(response)
                    context.commit('retrieveUser', response.data)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        retrieveUsers(context) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/users')
                .then(response => {
                    console.log(response)
                    context.commit('retrieveUsers', response.data)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        retrieveUserPhoto(context) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/user/image/receive')
                .then(response => {
                    context.commit('retrieveUserPhoto', response.data)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        register(context, data) {
            return new Promise((resolve, reject) => {
                axios.post('/register', {
                    name: data.name,
                    email: data.email,
                    password: data.password,
                })
                    .then(response => {
                        resolve(response)
                    })
                    .catch(error => {
                        reject(error)
                    })
            })
        },
        destroyToken(context) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token

            if (context.getters.loggedIn) {
                return new Promise((resolve, reject) => {
                    axios.post('/logout')
                        .then(response => {
                            localStorage.removeItem('access_token')
                            context.commit('destroyToken')
                            resolve(response)
                        })
                        .catch(error => {
                            localStorage.removeItem('access_token')
                            context.commit('destroyToken')
                            reject(error)
                        })
                })
            }
        },
        retrieveToken(context, credentials) {
            return new Promise((resolve, reject) => {
                axios.post('/login', {
                    username: credentials.username,
                    password: credentials.password,
                })
                    .then(response => {
                        const token = response.data.access_token

                        localStorage.setItem('access_token', token)
                        context.commit('retrieveToken', token)
                        resolve(response)
                        console.log(response);
                    })
                    .catch(error => {
                        console.log(error)
                        reject(error)
                    })
            })
        },
        deleteUser(context,user){
            return new Promise((resolve, reject) => {
                axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                axios.delete('user/delete/' + user.id
                    // eslint-disable-next-line no-unused-vars
                ).then( response => {
                        context.state.users = []
                        context.dispatch('retrieveUsers')
                }

                )
                    .catch(error => {
                        console.log(error)
                        reject(error)
                    })
            })
        },
        //END OF USER AND AUTH
        //LEADS
        retrieveLeads(context) {
            return new Promise((resolve, reject) => {
                axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                axios.get('/leads')
                    .then(response => {
                        console.log(response)
                        context.commit('retrieveLeads', response.data)
                        resolve(response)

                    })
                    .catch(error => {
                        console.log(error)
                        reject(error)
                    })
            })
        },
        updateLead(context, lead) {
            return new Promise((resolve, reject) => {
                axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                axios.put('/lead/' + lead.id, {
                    lead: {
                        "name": lead.name,
                        "email": lead.email,
                        "telephone": lead.telephone,
                        "date": lead.date,
                        "time_landing": lead.time_landing,
                        "time": lead.time,
                        "aktion": lead.aktion,
                        "quelle": lead.quelle,
                        "call_to_action": lead.call_to_action,
                        "page": lead.page,
                        "url": lead.url,
                        "status": lead.status,
                        "resubmission": lead.resubmission
                    }
                }).then(response => {
                    resolve(response)
                })
                    .catch(error => {
                        console.log(error)
                        reject(error)
                    })
            })

        },
        // END OF LEADS
        //COMMENTS
        fetchComment(context, commentId) {
            return new Promise((resolve, reject) => {
                console.log(commentId)
                axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                axios.get('/comment/' + commentId).then(response => {
                    context.commit('fetchComment', response.data[0].comment)
                    context.commit('retrieveAuthor', response.data[0].author)
                    resolve(response)

                }).catch(error => {
                    console.log(error)
                    reject(error)
                })
            })
        },
        postComment(context, comment) {
            console.log(context)
            console.log(comment.guid)
            console.log(comment.comment)

            return new Promise((resolve, reject) => {
                axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                axios.post('/comment/' + comment.guid, {
                    comment: {
                        'comment': comment.comment,
                        'author': context.state.user.name,
                        'guid': comment.guid
                    }
                }).then(response => {
                    resolve(response)
                }).catch(error => {
                    console.log(error)
                    reject(error)
                })

            })
        },
        //END OF COMMENTS

        //FILTER
        updateFilter(context, filter) {
            context.commit('updateFilter', filter)
        }
        ,
        // USER UPDATE FORM
        showUpdate(context) {
            this.showUpdate = !this.showUpdate
            context.commit('showUpdate', this.showUpdate)
        },
        // END OF USER UPDATE FORM
        //HISTORY
        retrieveHistory(context) {
            return new Promise((resolve, reject) => {
                axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                axios.get('/users').then(response => {
                        context.state.users = response.data
                    }
                ).then(axios.get('/history')
                    .then(response => {
                        response.data.forEach(audit => {
                            if (audit.event == 'updated') {
                                audit.old_values = audit.old_values.replaceAll('"', '').replaceAll('{', '').replaceAll('}', '')
                                audit.new_values = audit.new_values.replaceAll('"', '').replaceAll('{', '').replaceAll('}', '')
                                audit.created_at = audit.created_at.replace("T", "  ").slice(0, 20);
                            }
                            if (audit.event == 'deleted') {
                                audit.new_values = 'deleted'
                                audit.created_at = audit.created_at.replace("T", "  ").slice(0, 20);
                                audit.old_values = JSON.parse(audit.old_values).name
                            }
                            const index = context.state.users.findIndex(user => user.id == audit.user_id)
                            audit.user_id = context.state.users[index].name
                        })
                        console.log(response)
                        context.commit('retrieveHistory', response.data)
                        resolve(response)
                    })
                    .catch(error => {
                        console.log(error)
                        reject(error)
                    }))

            })
        }
            //END OF HISTORY
    }
})
