<template>
  <b-container fluid>
    <b-input-group size="sm" class="table-top">
      <b-form-input
          id="filter-input"
          v-model="filter"
          type="search"
          placeholder="Type to Search"
      ></b-form-input>
      <LeadsFilter class="filters"/>
    </b-input-group>
    <!-- Main table element -->

    <b-table
        :items="leadsFiltered"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        stacked="md"
        show-empty
        small
        @filtered="onFiltered"
    >
      <template #cell(update)="row">
        <PostUpdateLeads :lead="row.item"/>
      </template>
      <template #cell(actions)="row">
        <b-icon icon="flag" font-scale="1.5" v-if="row.item.resubmission == 0 || row.item.resubmission == false"
                @click="resubmission(row.item)" class="icons"></b-icon>
        <b-icon icon="flag-fill" v-if="row.item.resubmission == true || row.item.resubmission == 1"
                @click="resubmission(row.item)" font-scale="1.5" class="icons-red"></b-icon>
        <b-icon icon="trash" font-scale="1.5" @click="remove(row.item.id)" class="icons"></b-icon>
      </template>
      <template #row-details="row">
        <b-card>
          <ul>
            <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
          </ul>
        </b-card>
      </template>
    </b-table>

  </b-container>
</template>

<script>
import axios from "axios";
import PostUpdateLeads from "./PostUpdateLeads";
import LeadsFilter from "./LeadsFilter";

export default {
  components: {
    PostUpdateLeads,
    LeadsFilter,
  },
  data() {
    return {
      items: [],
      fields: [
        {key: 'id', label: 'ID', sortable: true, class: 'text-center'},
        {key: 'name', label: 'Name', sortable: true, class: 'text-center', sortDirection: 'desc'},
        {key: 'email', label: 'Email', sortable: true, class: 'text-center'},
        {key: 'telephone', label: 'Telephone', sortable: true, class: 'text-center'},
        {key: 'date', label: 'Date', sortable: true, class: 'text-center'},
        {key: 'aktion', label: 'Action', sortable: true, class: 'text-center'},
        {key: 'status', label: 'Status', sortable: true, class: 'text-center'},
        {key: 'actions', label: 'Actions', class: 'text-center'},
        {key: 'update', label: '', class: 'text-center'}
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, {value: 100, text: "Show a lot"}],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
    }
  },
  computed: {
    leadsFiltered() {
      return this.$store.getters.leadsFiltered
    },
  },
  mounted() {
    // Set the initial number of items
    this.totalRows = this.items.length
    this.fetchLeads()
  },
  methods: {
    fetchLeads() {
      this.$store.dispatch('retrieveLeads')
    },
    remove(id) {
      axios.delete('/lead/' + id)
          .then(
              this.fetchLeads())
    },
    resubmission(lead) {
      lead.resubmission === 0 || lead.resubmission === false ? lead.resubmission = true : lead.resubmission = false
      this.$store.dispatch('updateLead', lead)
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  }
}
</script>
<style>
.icons {
  margin-right: 10px
}

.icons-red {
  color: red;
  margin-right: 10px
}

.table-top {
  display: flex;
  justify-content: space-between
}

.filters {
  margin-top: 5px;
}
</style>