<template>
  <div class="user-container">
    <div class="user-container-left">
      <md-avatar>
        <img class="img" :src="`${this.$store.state.userURL}${photo}`"/>
        <md-tooltip v-if="access_level" md-direction="bottom">{{ access_level }}</md-tooltip>
      </md-avatar>
      <div class="user-label">{{ name }}
        <!--        <h6> {{ access_level }}</h6>-->
      </div>
    </div>
    <div class="remove-item">
      <DeleteModal :user="user" :show-dialog="false" v-if="this.showUser.id !== user.id"/>
      <UserModal :user="user" :showDialog="false"/>
    </div>
  </div>
</template>
<script>
import UserModal from "./UserModal";
import DeleteModal from "./DeleteModal";

export default {
  name: 'user',
  components: {DeleteModal, UserModal},
  data() {
    return {
      'id': this.user.id,
      'name': this.user.name,
      'about': this.user.about,
      'position': this.user.position,
      'description': this.user.description,
      'photo': this.user.file_path,
      'access_level': this.user.access_level,

    }
  },
  props: {
    user: {
      type: Object,
      required: true,
    },
  },
  computed: {
    showUsers() {
      return this.$store.getters.retrieveUsers
    },
    showUser() {
      return this.$store.getters.retrieveUser
    }
  },
  methods: {
    fetchUsers() {
      this.$store.dispatch('retrieveUsers')
    }
  },
  created() {
    this.fetchUsers()
  }
}
</script>
