<template>
  <div>
    <md-card md-with-hover>
      <md-card-header :data-background-color="dataBackgroundColor">
        <div class="md-title circles-title">Leads</div>
      </md-card-header>
      <md-card-content>
        <div class="circles-title-progress">
          <vue-ellipse-progress
              :progress="this.leadsFiltered.filter(lead => lead.date === this.datum).length  * 100 / this.leadsFiltered.length"
              color="#2f855a"
              empty-color="#324c7e"
              :size="180"
              :thickness="5"
              :empty-thickness="3"
              lineMode="out 5"
              legend
              animation="rs 700 1000"
              :legendValue="this.leadsFiltered.filter(lead => lead.date === this.datum).length"
              fontSize="1.5rem"
              font-color="black">
            <!--      <span slot="legend-value">/{{ this.leadsFiltered.length }}</span>-->
            <p slot="legend-caption">Leads Today</p>
          </vue-ellipse-progress>
          <vue-ellipse-progress
              :progress="this.leadsFiltered.filter(lead => lead.date === this.datum).length  * 100 / this.leadsFiltered.length"
              color="#2f855a"
              empty-color="#324c7e"
              :size="180"
              :thickness="5"
              :empty-thickness="3"
              lineMode="out 5"
              legend
              animation="rs 700 1000"
              :legendValue="this.leadsFiltered.filter(this.getWeek).length"
              fontSize="1.5rem"
              font-color="black">
            <!--      <span slot="legend-value">/{{ this.leadsFiltered.length }}</span>-->
            <p slot="legend-caption">Leads this Week</p>
          </vue-ellipse-progress>
          <vue-ellipse-progress
              :progress="this.leadsFiltered.filter(lead => lead.date === this.datum).length  * 100 / this.leadsFiltered.length"
              color="#2f855a"
              empty-color="#324c7e"
              :size="180"
              :thickness="5"
              :empty-thickness="3"
              lineMode="out 5"
              legend
              animation="rs 700 1000"
              :legendValue="this.leadsFiltered.filter(lead => lead.date.slice(5, 7) === ('0' + (this.today.getMonth() + 1)).slice(-2)).length"
              fontSize="1.5rem"
              font-color="black">
            <!--      <span slot="legend-value">/{{ this.leadsFiltered.length }}</span>-->
            <p slot="legend-caption">Leads this month</p>
          </vue-ellipse-progress>
        </div>
      </md-card-content>
    </md-card>
    <md-card md-with-hover>
      <md-card-header :data-background-color="dataBackgroundColor">
        <div class="md-title circles-title">Status</div>
      </md-card-header>
      <md-card-content class="circles-title-progress">
        <vue-ellipse-progress
            :progress="this.leadsFiltered.filter(lead => lead.status == 'Abgesclossen').length  * 100 / this.leadsFiltered.length"
            color="#2f855a"
            empty-color="#324c7e"
            :size="180"
            :thickness="5"
            :empty-thickness="3"
            lineMode="out 5"
            legend
            animation="rs 700 1000"
            :legendValue="this.leadsFiltered.filter(lead => lead.status == 'Abgesclossen').length"
            fontSize="1.5rem"
            font-color="black">
          <!--          <span slot="legend-value">/{{ this.leadsFiltered.length }}</span>-->
          <p slot="legend-caption">Status Abgesclossen</p>
        </vue-ellipse-progress>
        <vue-ellipse-progress
            :progress="this.leadsFiltered.filter(lead => lead.status == 'Bearbeitung').length  * 100 / this.leadsFiltered.length"
            color="#2f855a"
            empty-color="#324c7e"
            :size="180"
            :thickness="5"
            :empty-thickness="3"
            lineMode="out 5"
            legend
            animation="rs 700 1000"
            :legendValue="this.leadsFiltered.filter(lead => lead.status == 'Bearbeitung').length"
            fontSize="1.5rem"
            font-color="black">
          <!--          <span slot="legend-value">/{{ this.leadsFiltered.length }}</span>-->
          <p slot="legend-caption">Status Bearbeitung</p>
        </vue-ellipse-progress>
        <vue-ellipse-progress
            :progress="this.leadsFiltered.filter(lead => lead.status == 'Neu').length  * 100 / this.leadsFiltered.length"
            color="#2f855a"
            empty-color="#324c7e"
            :size="180"
            :thickness="5"
            :empty-thickness="3"
            lineMode="out 5"
            legend
            animation="rs 700 1000"
            :legendValue="this.leadsFiltered.filter(lead => lead.status == 'Neu').length"
            fontSize="1.5rem"
            font-color="black">
          <!--      <span slot="legend-value">/{{ this.leadsFiltered.length }}</span>-->
          <p slot="legend-caption">Status Neu</p>
        </vue-ellipse-progress>
      </md-card-content>
    </md-card>
  </div>
</template>
<script>
import { DateTime } from 'luxon'

export default {
  name: "progress",
  props: {
    dataBackgroundColor: {
      type: String,
      default: "green"
    }
  },
  data() {
    return {
      today: new Date(),
      datum: null,
      weekNumber: null
    };
  },
  created() {
    this.$store.dispatch('retrieveLeads')
    this.datum = this.today.getFullYear() + '-' + ("0" + (this.today.getMonth() + 1)).slice(-2) + '-' + (this.today.getDate());
    this.weekNumber = DateTime.utc().weekNumber
  },
  computed: {
    leadsFiltered() {
      return this.$store.getters.leadsFiltered
    },
  },
  methods: {
    getWeek(lead) {
      var leadDate = lead.date.split('-')

      var LeadWeekNumber = DateTime.utc(parseInt(leadDate[0]) , parseInt(leadDate[1]) , parseInt(leadDate[2])).weekNumber

      if (LeadWeekNumber == this.weekNumber) {
        return lead
      }
    }
  }
};

</script>
