<template>
  <div>
    <md-dialog :md-active.sync="showDialog">
      <md-dialog-content>
        <md-card class="md-card-profile">
          <div class="md-card-avatar" @click="$refs.file.click()">
            <img :src="this.url ? this.url : `${this.$store.state.userURL}${user.file_path}`">

            >
            <md-tooltip md-direction="bottom">Change Photo</md-tooltip>
            <input type="file" ref="file" style="display: none" @change="uploadImage(user.id)">
          </div>

          <md-card-content>
            <div class="md-layout lead-update-form">
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field :class="messageClass">
                  <label>Change name</label>
                  <md-input v-model="user.name"></md-input>
                  <span class="md-error">required</span>
                </md-field>
              </div>

              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Change Position</label>
                  <md-input v-model="user.position">
                  </md-input>
                </md-field>
              </div>

              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Change Description</label>
                  <md-input v-model="user.about">
                  </md-input>
                </md-field>
              </div>

              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field :class="messageClass">
                  <label>Change Email</label>
                  <md-input v-model="user.email"></md-input>
                  <span class="md-error">Required</span>
                </md-field>
              </div>

              <div class="md-layout-item md-small-size-100 md-size-50">
                <md-field>
                  <label>Change Access</label>
                  <md-select v-model="user.access_level">
                    <md-option value="Admin">Admin</md-option>
                    <md-option value="Mitarbeiter">Mitarbeiter</md-option>
                  </md-select>
                </md-field>
              </div>
            </div>
            <md-button class="md-round md-success" @click="updateUser(user)">Update profile</md-button>
          </md-card-content>
        </md-card>


      </md-dialog-content>
    </md-dialog>
    <div @click="showDialog = true">
      <md-icon>mode_edit</md-icon>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'DialogCustom',
  props: {
    user: {
      type: Object,
      required: true,
    },
    showDialog: {
      type: Boolean,
      required: true
    }
  },
  data: () => ({
    // 'id': this.user.id,
    // 'name': this.user.name,
    // 'email': this.user.email,
    // 'about': this.user.about,
    // 'position': this.user.position,
    // 'description': this.user.description,
    // 'photo': this.user.file_path,
    // 'access': this.user.access,
    url: null,
    image: null,
    incorrect: null,
  }),
  computed: {
    messageClass() {
      return {
        "md-invalid": this.incorrect,
      };
    },
  },
  methods: {
    fetchUsers() {
      this.$store.dispatch('retrieveUsers')
    },
    // eslint-disable-next-line no-unused-vars
    uploadImage(userId) {
      this.image = event.target.files[0]

      if (this.image.size > 2097152) {
        alert('Max size 2MB')
      } else {
        this.url = URL.createObjectURL(this.image);
      }
    },
    updateUser(user) {
      if (this.image) {
        const fd = new FormData();
        fd.append('image', this.image)
        axios.post('/user/image/upload/' + user.id, fd)
            // eslint-disable-next-line no-unused-vars
            .then(response => {
              this.fetchUsers()
              this.$store.dispatch('retrieveUsers')
              this.$store.dispatch('retrieveUserPhoto')

            })
      }
      if (user.email.length <= 0 || user.name.length <= 0) {
        this.incorrect = true
      } else {
        this.incorrect = false
        axios.put('/user/update/' + user.id, {
          user: {
            "name": user.name,
            'email': user.email,
            'about': user.about,
            'position': user.position,
            'access_level': user.access_level
          }
        }).then(
            this.$store.state.users = [],
            this.fetchUsers()
        )
      }
    }
  },
  created() {
    this.fetchUsers()
  }
}
</script>

<style lang="scss" scoped>
.md-dialog.md-dialog-container {
  max-width: 768px;
}
</style>