<template>
  <div>
    <div class="user-list-container">
      <div class="user-list">
        <md-card>
          <md-card-header :data-background-color="dataBackgroundColor" class="user-card-header">
            <h4 class="title circles-title">Users</h4>
            <AddUser :showDialog="false" class="add-user"/>
          </md-card-header>
          <md-card-content>
            <User v-for="user in showUsers" :key="user.file_path" :user="user"/>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import User from "./User";
import AddUser from "./AddUser";

export default {
  name: 'users',
  components: {AddUser, User},
  props: {
    dataBackgroundColor: {
      type: String,
      default: "green"
    }
  },
  computed: {
    showUsers() {
      return this.$store.getters.retrieveUsers
    }
  },
  methods: {
    fetchUsers() {
      this.$store.dispatch('retrieveUsers')
    }
  },
  created() {
    this.fetchUsers()
  }
}
</script>

<style>
.user-list {
  width: 75%;
  display: flex;
  justify-content: center;
}
.add-user {
  cursor: pointer;
}
.user-list-container {
  display: flex;
  justify-content: center
}
.user-card-header {
  display:flex;
  justify-content: space-between
}
</style>